奚適。嗟歎不足，聊書此以冠諸篇首。」其後繼作不絕，溢乎箱篋。其述形似，則有張禮部云：「奔蛇走虺勢入座，驟雨旋風聲滿堂。」盧員外云：「初疑輕煙澹古松，又似山開萬仞峰。」王永州邕曰：「寒猿飲水撼枯藤，壯士拔山伸勁鐵。」朱處士遙云：「筆下唯看激電流，字成只畏盤龍走。」敘機格，則有李御史舟云：「昔張旭之作也，時人謂之張顛。今懷素之為也，余實謂之狂僧。以狂繼顛，誰曰不可？」張公又云：「稽山賀老粗知名，吳郡張顛曾不面。」許御史瑤云：「志在新奇無定則，古瘦灕驪半無墨。醉來信手兩三行，醒後卻書書不得。」戴御史叔倫云：「心手相師勢轉奇，詭形恠狀翻合宜。人人欲問此中妙，懷素自言初不知。」語疾速，則有竇御史冀云：「粉壁長廊數十間，興來小豁胸中氣。忽然絕叫三五聲，滿壁縱橫千萬字。」戴公又云：「馳豪驟墨劇奔駟，滿座失聲看不及。」目愚劣。則有從父司勳員外郎吳興錢起詩云：「遠鶴無前侶，孤雲寄太虛。狂來輕世界，醉裏得真如。」皆辭旨激切，理識玄奧，固非虛蕩之所敢當，徒增愧畏耳。時大曆丁巳冬十月廿有八日。")])])])])])]),_c('swiper',{staticClass:"swiper",staticStyle:{"background":"#8c3c31","bottom":"-10%"},attrs:{"options":_vm.swiperOption}},[_vm._l((_vm.items),function(item,i){return _c('swiper-slide',{key:i,staticStyle:{"height":"100vh"}},[_c('img',{attrs:{"src":item.src,"height":"100%"}})])}),_c('div',{staticClass:"swiper-pagination",attrs:{"slot":"pagination"},slot:"pagination"}),_c('div',{attrs:{"slot":"button-prev"},slot:"button-prev"}),_c('div',{attrs:{"slot":"button-next"},slot:"button-next"})],2),_c('swiper',{staticClass:"swiper",staticStyle:{"position":"absolute","bottom":"0"}},[_c('swiper-slide'),_c('img',{staticClass:"hongyun",attrs:{"src":require("../assets/hongyun.jpg")}}),_c('img',{staticClass:"hongyun2",attrs:{"src":require("../assets/hongyun.jpg")}}),_c('div',{staticClass:"swiper-button-prev"}),_c('div',{staticClass:"swiper-button-next"})],1),_c('MovePosition')],1)])}
